import type ClientsPresenceState from '../models/ClientsPresenceState'
/**
 * This is used after a successful `update_client_presence` and will set the new `presenceContent`
 * inside the `currentClient` and the current client inside the `clients` map.
 */

export default function updateCurrentClientPresenceAction(
  state: ClientsPresenceState,
  presenceContent: Record<string, any>,
): ClientsPresenceState {
  return state
    .setIn(['currentClient', 'presenceContent'], presenceContent)
    .setIn(['clients', state.currentClient?.clientId, 'presenceContent'], presenceContent)
}
