import * as Immutable from 'immutable'

interface IRecordChangeRequest {
  id: string | null
  content?: Record<string, any> | null
  attachments: Record<string, Blob> | null
  group?: string
  type: 'created' | 'updated' | 'deleted' | null
  resolve: (response?: Record<string, any> | boolean | null) => void
  reject: (error?: any) => void
}

export default class RecordChangeRequest extends Immutable.Record<IRecordChangeRequest>({
  content: null,
  attachments: null,
  id: null,
  type: null,
  group: undefined,
  resolve: () => {},
  reject: () => {},
}) {
  /**
   * Holds the record ID as a string. This ID must follow the Instant spec.
   *
   * @member {string} id
   */
  override id: string

  /**
   * Holds the serialized record content.
   *
   * @member {?Object} content
   */
  override content?: Record<string, any>

  /**
   * Holds an Object where the annotations ids are mapped to the Blobs of the records attachments.
   *
   * @member {?Object} attachments
   */
  override attachments: Record<string, Blob>

  /**
   * Holds the group name of the record
   *
   * @type {string|void}
   */
  override group?: string

  /**
   * Changes can be `created` for new records, `updated` for existing
   * records with a new content, or `deleted` for deleted records.
   *
   * @member {string} type
   */
  override type: 'created' | 'updated' | 'deleted'

  /**
   * Holds the `Promise#resolve` function for the change request
   *
   * @member {Function} resolve
   */
  override resolve: (response?: Record<string, any> | boolean | null) => void

  /**
   * Holds the `Promise#reject` function for the change request
   *
   * @member {Function} reject
   */
  override reject: (error?: any) => void
}
