import { invariant } from '@pspdfkit/shared'
import Client from '../models/Client'

/**
 * Used to map a payload entry for a client to a `Client`
 */
export function clientFromPayload(payload: Record<string, any>): Client {
  invariant(typeof payload.client_id === 'string', 'The client payload must have a `client_id`')
  invariant(typeof payload.presence === 'object', 'The client payload must have a `presence`')

  return new Client({
    clientId: payload.client_id,
    userId: payload.user_id,
    presenceContent: payload.presence,
  })
}
