import { getEnvironment } from '@/utils/environment'
import { Map } from 'immutable'
import EventEmitter from '../EventEmitter'
import { PSPDFKitError } from '@pspdfkit/shared'
import Timer from '../Timer'
import { invariant } from '@/utils/invariant'
import { IObject } from '@/typings/utils'

const CLIENT_VERSION = '0.0.1'
const PROTOCOL_VERSION = 2
const SOCKET_STATES = {
  connecting: 0,
  open: 1,
  closing: 2,
  closed: 3,
}
const CLIENT_INFO = {
  name: 'PSPDFKit-Web',
}

const defaultReconnectTimerCalc = n => [1000, 2000][n - 1] || 5000
/**
 * SYNCConnection
 *
 * Abstracts the web socket and the sync protocol. This class behaves like an event emitter, which
 * means that we can listen on any incoming message using the `.on('type', callback)` method.
 *
 * In case the connection gets interrupted, we try to reconnect in a specified time interval. The
 * next time a reconnect is successful, we will emit the `reconnect` event.
 *
 * A SYNCConnection will publish the following events:
 *   - `connect` when the connection was authenticated with the argument
 *     `{ userId: ?string, clientId: string }`. This will also be called when the connection was
 *     closed and we have successfully reconnected. In theory the same token could then be used to
 *     authenticated a different user.
 *   - `disconnect` when the connection was interrupted. Use this to show a disconnect message until
 *     we emit the `authenticated` action again.
 *   - `error` when we discover a non-recoverable error during the authentication flow. In this
 *     case, we will try to reconnect. This can happen when the auth token is invalid.
 *   - `record_changes` when the server sends the analogue info request. This will simply forward
 *     the same payload
 *   - `client_presence` when the server sends the analogue info request. This will simply forward
 *     the same payload
 *
 * @private
 */

export default class WebsocketConnection {
  eventEmitter: EventEmitter
  serverURL: string
  authPayload: IObject
  socket: WebSocket | null
  lastRequestId: number
  requestsWaitingForAnswers: Map<
    number,
    {
      resolve: (payload: IObject) => void
      reject: (error: Error) => void
    }
  >
  reconnectTimer: Timer | null
  clientId: string
  userId: string | null

  constructor(
    serverURL: string,
    authPayload: IObject,
    {
      reconnectTimerCalc = defaultReconnectTimerCalc,
      enableReconnect = true,
      events = [],
    }: {
      reconnectTimerCalc?: (n: number) => number
      enableReconnect?: boolean
      events?: Array<string>
    } = {},
  ) {
    this.eventEmitter = new EventEmitter(['connect', 'disconnect', 'error'].concat(events))
    this.serverURL = serverURL
    this.authPayload = authPayload
    this.socket = null
    this.lastRequestId = 0
    this.requestsWaitingForAnswers = Map()

    if (enableReconnect) {
      this.reconnectTimer = new Timer(() => {
        if (this.socket) {
          this.socket.close()
          this.socket = null
        }

        this.connect()
      }, reconnectTimerCalc)
    }

    this.clearAuthenticationInformation()
  }

  registerEvents(events: Array<string>): void {
    this.eventEmitter.events.push(...events)
  }

  connect() {
    if (this.socket) {
      return
    }

    const socket = new WebSocket(this.serverURL)

    socket.onopen = this.onOpen.bind(this)
    socket.onmessage = this.onMessage.bind(this)

    socket.onerror = () => {
      this.socket = null
      this.eventEmitter.emit(
        'error',
        `Failed to create the WebSocket connection to ${this.serverURL}. ` +
          'Please check your firewall or proxy settings.',
      )
    }

    this.socket = socket
  }

  /**
   * Closes the socket permanently. This will `.reject()` all outstanding request
   */
  disconnect(): void {
    if (!this.socket) {
      return
    }

    // unbind existing closing event so we don't try to reconnect
    this.socket.onclose = () => {}

    this.socket.close()
    this.clearAuthenticationInformation()
    this.abortOpenRequests()
    this.eventEmitter.emit('disconnect')
  }

  /**
   * @returns {string} The current connection state. Either `connecting`, `open`, `closing`, or
   *          `closed`
   */
  get connectionState(): 'connecting' | 'open' | 'closing' | 'closed' {
    switch (this.socket && this.socket.readyState) {
      case SOCKET_STATES.connecting:
        return 'connecting'

      case SOCKET_STATES.open:
        return 'open'

      case SOCKET_STATES.closing:
        return 'closing'

      default:
        return 'closed'
    }
  }

  /**
   * @returns {boolean} true when the connection is authenticated
   */
  get isAuthenticated(): boolean {
    return this.clientId !== ''
  }

  /**
   * Sends data asynchronously to the server. This will generate a new request ID. Whenever the
   * server responds, we will resolve the Promise.
   *
   * @param {string} action - The action you want to send
   * @param {Object} payload - An arbitrary payload. This will be serialized as JSON
   * @returns {Promise.<Object, PSPDFKitError>} A promise that will resolve as soon as the server responds to our request.
   */
  sendRequest(action: string, payload: IObject): Promise<IObject> {
    return new Promise((resolve, reject) => {
      if (!this.isAuthenticated || !this.socket) {
        reject(new PSPDFKitError('Cannot send request when the connection is not authenticated'))

        return
      }

      const requestId = this.nextRequestId()
      const serializedPayload = JSON.stringify(payload)

      this.requestsWaitingForAnswers = this.requestsWaitingForAnswers.set(requestId, {
        resolve,
        reject,
      })

      const socket: WebSocket = this.socket

      socket.send(`${requestId}:${action}:${serializedPayload}`)
    })
  }

  // `on` and `off` will be forwarded to `this.eventEmitter`
  on(
    action: 'connect' | 'disconnect' | 'error' | string,
    callback: (payload: IObject) => void,
  ): void {
    this.eventEmitter.on(action, callback)
  }

  off(
    action: 'connect' | 'disconnect' | 'error' | string,
    callback: (payload: IObject) => void,
  ): void {
    this.eventEmitter.off(action, callback)
  }

  /**
   * Web socket callbacks
   */

  private onOpen() {
    const socket = this.socket

    if (socket) {
      socket.onerror = this.onError.bind(this)
      socket.onclose = this.onClose.bind(this)
    }
  }

  private onMessage(message: MessageEvent) {
    const data = message.data as string

    if (this.isAuthenticated) {
      const frame = this.parseFrame(data)

      // Check whether this is a notification from the server or a response to a request
      if (frame.requestId) {
        const requestId = frame.requestId

        invariant(
          this.requestsWaitingForAnswers.has(requestId),
          'Received a reply with an unknown request ID.',
        )

        const request = this.requestsWaitingForAnswers.get(requestId)

        invariant(request)

        switch (frame.action) {
          case 'ok':
            request.resolve(frame.payload)
            break

          case 'error':
            request.reject(new PSPDFKitError(frame.payload.reason || 'Unknown error'))
            break

          default:
            invariant(false, `${frame.action} is not a valid request reply`)
        }

        this.requestsWaitingForAnswers = this.requestsWaitingForAnswers.delete(requestId)
      } else {
        if (this.eventEmitter.events.includes(frame.action)) {
          this.eventEmitter.emit(frame.action, frame.payload)
        }

        this.log('incoming info message', frame)
      }
    } else {
      const frame = this.parseUnauthenticatedFrame(data)

      // When the request is not yet authenticated, we only handle `hello` and `authenticated`
      // messages
      switch (frame.action) {
        case 'hello':
          this.onHello(frame.payload)
          break

        case 'authenticated':
          this.onAuthenticated(frame.payload)
          break

        case 'error':
          this.eventEmitter.emit('error', frame.payload.reason || 'Unknown error')
      }
    }
  }

  /**
   * When the connection is closed, we will send the `disconnect` event so the client can display
   * an adequate message. This will start our regular reconnection timer.
   */
  private onClose(event: CloseEvent) {
    this.clearAuthenticationInformation()
    this.abortOpenRequests()
    this.reconnectTimer && this.reconnectTimer.scheduleTimeout()
    this.eventEmitter.emit('disconnect')
    this.log('close', event)
  }

  /**
   * We will receive an error callback when there were problems opening the web socket. This will
   * start our regular reconnection timer.
   */
  private onError(error: Event) {
    this.reconnectTimer && this.reconnectTimer.scheduleTimeout()
    this.log('error', error)
  }

  private nextRequestId(): number {
    // We deliberately don't care about integer overflow of the request id. We will handle this in
    // the future by reconnecting the client
    const nextRequestId = this.lastRequestId + 1

    this.lastRequestId = nextRequestId

    return nextRequestId
  }

  private onHello(payload: IObject): void {
    const socket: WebSocket = this.socket as any

    if (payload.protocol_version === PROTOCOL_VERSION) {
      const requestPayload = {
        protocol_version: PROTOCOL_VERSION,
        client_version: CLIENT_VERSION,
        client_info: CLIENT_INFO,
        auth_payload: this.authPayload,
      }

      socket.send(`hello_web:${JSON.stringify(requestPayload)}`)
    } else {
      const requestPayload = {
        reason: 'protocol_mismatch',
        protocol_version: PROTOCOL_VERSION,
        client_version: CLIENT_VERSION,
        client_info: CLIENT_INFO,
      }

      socket.send(`handshake_failed:${JSON.stringify(requestPayload)}`)
      this.eventEmitter.emit('error', 'protocol_mismatch')
    }
  }

  private onAuthenticated(payload: IObject): void {
    invariant(payload.client_id, '`authenticated` message has no `client_id`')
    this.clientId = payload.client_id
    this.userId = payload.user_id || null
    this.eventEmitter.emit('connect', {
      clientId: this.clientId,
      userId: this.userId,
    })
  }

  private log(...args: Array<any>): void {
    if (getEnvironment() === 'development') {
      console.log('SYNCConnection', ...args) // eslint-disable-line no-console
    }
  }

  /**
   * When `requestId` in the response object is null, the frame is of type `info`.
   */
  private parseFrame(frame: string): {
    requestId?: number | null
    action: string
    payload: IObject
  } {
    const regex = /^(\d+|info):([a-zA-Z-_]+):(.+)$/
    const [, requestIdOrInfo, action, serializedPayload] = regex.exec(
      frame.toString(),
    ) as RegExpExecArray
    let requestId: number | null = null

    if (requestIdOrInfo !== 'info') {
      requestId = parseInt(requestIdOrInfo)
    }

    const payload = JSON.parse(serializedPayload)

    return {
      requestId,
      action,
      payload,
    }
  }

  private parseUnauthenticatedFrame(frame: string): {
    action: string
    payload: IObject
  } {
    const regex = /^(hello|authenticated|error):(.+)$/

    const [, action, serializedPayload] = regex.exec(frame.toString()) as RegExpExecArray
    const payload = JSON.parse(serializedPayload)

    return {
      action,
      payload,
    }
  }

  /** @private **/
  abortOpenRequests(): void {
    this.requestsWaitingForAnswers.forEach(request => {
      request.reject(new PSPDFKitError('request aborted'))
    })
    this.requestsWaitingForAnswers = Map()
  }

  /** @private **/
  clearAuthenticationInformation(): void {
    this.clientId = ''
    this.userId = null
  }
}
