import { Class } from 'utility-types'
import { List, Map, Set } from 'immutable'
import { error, warn, PSPDFKitError, info, invariant } from '@pspdfkit/shared'
import {
  deserializeAnnotation,
  deserializeFormFieldValue,
  isBookmarkJSON,
  isFormFieldJSON,
  isFormFieldValueJSON,
  serializeAnnotation,
  attachmentsToObject,
  serializeFormFieldValue,
  serializeFormField,
  deserializeFormField,
  serializeComment,
  isCommentJSON,
  deserializeComment,
  isSignatureInfoJSON,
  isEmbeddedFileJSON,
} from '../serializers/utils'
import {
  toJSON as serializeBookmark,
  fromJSON as deserializeBookmark,
} from '../serializers/BookmarkSerializer'
import { CREATE_REASON_LOAD, CREATE_REASON_NEW } from '../Provider'
import ClientsPresence from '../ClientsPresence/ClientsPresence'
import InstantSync from './InstantSync'
import { defaultInstantSettings } from '@/models/InstantSettings'
import { getFormFieldValueID } from '@/models/form-fields/FormFieldValue'
import { isRecordInState } from './collaboration-permissions'
import Annotation from '@/models/annotations/Annotation'
import Comment from '@/models/comments/Comment'
import FormField from '@/models/form-fields/FormField'
import { getRecordInState } from './collaboration-permissions'
import {
  getCollaborationPermissionsFromJSON,
  validateCollaborationPermissionJSON,
} from '../serializers/collaboration-permissions'
import type { ReadStateProvider, ReadStateProviderCallbacks } from '../ReadStateProvider'
import type { AnnotationProvider, AnnotationProviderCallbacks } from '../AnnotationProvider'
import type { CreateReason } from '../Provider'
import { Attachment, Bookmark, WidgetAnnotation } from '@/models'
import type { BookmarkProvider, BookmarkProviderCallbacks } from '../BookmarkProvider'
import { FormFieldValue, SignatureFormField } from '@/models/form-fields'
import type { FormFieldProvider, FormFieldProviderCallbacks } from '../FormFieldProvider'
import type { FormFieldValueProvider, FormFieldValueCallbacks } from '../FormFieldValueProvider'
import type { ID as AnnotationID } from '@/models/annotations/Annotation'
import type { ID as BookmarkID } from '@/models/Bookmark'
import type { ID as CommentID } from '@/models/comments/Comment'
import type { InstantSyncAuthPayload } from './InstantSyncState'
import type Client from '../ClientsPresence/models/Client'
import type { InstantID } from '@/models/InstantID'
import type { CommentProvider, CommentProviderCallbacks } from '../CommentProvider'
import type { InstantSettings } from '@/models/InstantSettings'
import type { State } from '@/models'
import { shouldShowSignatureFields } from '@/utils/permissions'
import { ISignatureFeatureAvailability } from '@/enums/SignatureFeatureAvailability'

export type IRecord = {
  id: string
  content?: Record<string, any> | null
  group?: string | null | undefined
  permissions?: {
    edit: boolean
    delete: boolean
    view: boolean
    setGroup: boolean
  }
}

/**
 * InstantProvider is an implementation of the AnnotationProvider that,
 * using the `/sync` endpoint, allows the client to sync with other devices and
 * benefit from server side conflict resolution.
 *
 * `createAnnotation` and `updateAnnotation` perform optimistic updates which are reverted
 * in case of conflicts with newer updates coming from the server.
 * `deleteAnnotation` instead is signed off only after the server has accepted the change
 * since in this case roll back and restore the annotation would be harder.
 */
export class InstantProvider
  implements
    ReadStateProvider,
    AnnotationProvider,
    BookmarkProvider,
    FormFieldProvider,
    FormFieldValueProvider,
    CommentProvider
{
  _readStateCallbacks?: ReadStateProviderCallbacks
  _annotationCallbacks?: AnnotationProviderCallbacks
  _bookmarkCallbacks?: BookmarkProviderCallbacks
  _formFieldCallbacks?: FormFieldProviderCallbacks
  _formFieldValueCallbacks?: FormFieldValueCallbacks
  _commentCallbacks?: CommentProviderCallbacks
  // We keep track of the bookmarks IDs when we create new bookmarks
  // because Instant sends a List of IDs when some deletion occurred.
  // In such cases we can use this list to determine whether we are dealing with
  // a bookmarks or annotation deletion.
  _existingBookmarksIds: Set<BookmarkID> = Set()
  _existingFormFieldsIds: Set<AnnotationID> = Set()
  _existingFormFieldValuesIds: Set<string> = Set()
  _existingCommentIds: Set<InstantID> = Set()
  _sync: InstantSync
  _clients?: ClientsPresence
  _documentURL: string
  _serverURL: string
  _settings: InstantSettings
  _authPayload: InstantSyncAuthPayload
  _hasLoadedInitialRecords: boolean
  _loadPromise: Promise<void> | null | undefined
  _getState: () => State
  _formsEnabledInConfig: boolean
  onClientsChangeCallback: (...args: Array<any>) => any
  _documentHandleConflictCallback: () => void = () => {}
  _setDocumentHandleOutdatedCallback: (arg0: boolean) => void
  canCreateBackendOrphanWidgets = true

  constructor(
    serverURL: string,
    documentURL: string,
    authPayload: InstantSyncAuthPayload,
    settings: InstantSettings = defaultInstantSettings,
  ) {
    this._serverURL = serverURL
    this._documentURL = documentURL
    this._authPayload = authPayload
    this._settings = settings
    this._hasLoadedInitialRecords = false
  }

  load(
    SyncClass: Class<InstantSync> = InstantSync,
    ClientsPresenceClass: Class<ClientsPresence> = ClientsPresence,
  ): Promise<InstantProvider> {
    const initializationPromises: Promise<any>[] = []

    // Initialize sync.
    this._sync = new SyncClass()
    initializationPromises.push(
      this._sync
        .load(
          `${this._documentURL}/sync`,
          this._authPayload,
          this._settings.listenToServerChangesEnabled,
        )
        .catch(error),
    )

    this._sync.setOnDocumentHandleConflictCallback(this.onDocumentHandleConflict)

    if (this._settings.clientsPresenceEnabled) {
      // Initialize clients presence.
      this._clients = new ClientsPresenceClass()
      initializationPromises.push(
        this._clients
          .load(`${this._serverURL.replace(/^http/i, 'ws')}/websocket`, this._authPayload, {})
          .then(() => {
            const clients = this._clients

            if (clients == null) return

            // Register callback
            clients.onClientUpdates(() => this._onClientsChange(clients.getClients()))

            // Handle initial clients the same way as a clients updates callback
            this._onClientsChange(clients.getClients())
          })
          .catch(error => {
            warn(
              'PSPDFKit: An error occurred while initializing the connected clients module. ' +
                'This might be due to a lack of support for WebSockets or a related failure.\n\n' +
                'Failure details:\n\n' +
                error.message,
            )
          }),
      )
    }

    return Promise.all(initializationPromises)
      .then(() => {
        return this
      })
      .catch(error => {
        throw new PSPDFKitError(`Initialization of PSPDFKit Instant failed:
${error.message}`)
      })
  }

  destroy() {
    if (this._sync) {
      this._sync.destroy()
    }
  }

  setFormsEnabledInConfig(formsEnabledInConfig: boolean) {
    this._formsEnabledInConfig = formsEnabledInConfig
  }

  setReadStateCallbacks(readStateCallbacks: ReadStateProviderCallbacks) {
    this._readStateCallbacks = readStateCallbacks
  }

  setAnnotationCallbacks(annotationCallbacks: AnnotationProviderCallbacks) {
    this._annotationCallbacks = annotationCallbacks
  }

  setBookmarkCallbacks(bookmarkCallbacks: BookmarkProviderCallbacks) {
    this._bookmarkCallbacks = bookmarkCallbacks
  }

  setFormFieldCallbacks(formFieldCallbacks: FormFieldProviderCallbacks) {
    this._formFieldCallbacks = formFieldCallbacks
  }

  setFormFieldValueCallbacks(formFieldValueCallbacks: FormFieldValueCallbacks) {
    this._formFieldValueCallbacks = formFieldValueCallbacks
  }

  setCommentCallbacks(commentCallbacks: CommentProviderCallbacks) {
    this._commentCallbacks = commentCallbacks
  }

  createAnnotation(annotation: Annotation, attachments: Map<string, Attachment>): Promise<void> {
    const { id, ...data } = serializeAnnotation(annotation)
    // destructuring permissions to remove if from content.
    const { group, permissions, ...content } = data

    return this._sync.createRecord(id, content, attachmentsToObject(attachments), group)
  }

  createComment(comment: Comment): Promise<void> {
    const { id, ...data } = serializeComment(comment)
    // destructuring permissions to remove if from content.
    const { group, permissions, ...content } = data

    this._existingCommentIds = this._existingCommentIds.add(id)

    return this._sync.createRecord(id, content, {}, group)
  }

  async updateComment(comment: Comment): Promise<void> {
    try {
      // @ts-expect-error
      return await this.updateRecord(serializeComment(comment))
    } catch (e) {
      if (!(e instanceof PSPDFKitError)) {
        throw e
      }
    }
  }

  deleteComment(commentId: InstantID): Promise<void> {
    this._existingCommentIds = this._existingCommentIds.delete(commentId)

    return this._sync.deleteRecord(commentId).then(() => {})
  }

  setStateGetter(getState: () => State): void {
    this._getState = getState
  }

  async updateRecord(data: Record<string, any>): Promise<Record<string, any>> {
    const { id, permissions, group, ...content } = data

    if (
      this._getState &&
      this._getState() &&
      this._getState().backend?.isCollaborationPermissionsEnabled()
    ) {
      return this._sync.updateRecord(
        id,
        permissions.edit ? content : undefined,
        permissions.setGroup ? group : undefined,
      )
    } else {
      return this._sync.updateRecord(id, content, group)
    }
  }

  async updateAnnotation(annotation: Annotation): Promise<void> {
    try {
      // @ts-expect-error
      return await this.updateRecord(serializeAnnotation(annotation))
    } catch (e) {
      if (!(e instanceof PSPDFKitError)) {
        throw e
      }
    }
  }

  deleteAnnotation(annotation: Annotation): Promise<void> {
    return this._sync.deleteRecord(annotation.id).then(() => {})
  }

  createBookmark(bookmark: Bookmark): Promise<void> {
    const { id, ...content } = serializeBookmark(bookmark)

    this._existingBookmarksIds = this._existingBookmarksIds.add(id)

    return this._sync.createRecord(id, content, {})
  }

  async updateBookmark(bookmark: Bookmark): Promise<void> {
    const { id, ...content } = serializeBookmark(bookmark)

    try {
      // @ts-expect-error
      return await this._sync.updateRecord(id, content)
    } catch (e) {
      if (!(e instanceof PSPDFKitError)) {
        throw e
      }
    }
  }

  deleteBookmark(bookmarkId: BookmarkID): Promise<void> {
    return this._sync.deleteRecord(bookmarkId).then(() => {
      this._existingBookmarksIds = this._existingBookmarksIds.delete(bookmarkId)
    })
  }

  createFormField(formField: FormField): Promise<void> {
    const { id, ...data } = serializeFormField(formField)
    // destructuring permissions to remove if from content.
    const { group, permissions, ...content } = data

    this._existingFormFieldsIds = this._existingFormFieldsIds.add(id)

    return this._sync.createRecord(id, content, {}, group)
  }

  async updateFormField(formField: FormField): Promise<void> {
    try {
      // @ts-expect-error
      return await this.updateRecord(serializeFormField(formField))
    } catch (e) {
      if (!(e instanceof PSPDFKitError)) {
        throw e
      }
    }
  }

  deleteFormField(formField: FormField): Promise<void> {
    return this._sync.deleteRecord(formField.id).then(() => {
      this._existingFormFieldsIds = this._existingFormFieldsIds.delete(formField.id)
    })
  }

  // Form fields are retrieved in the annotations list
  loadFormFields(): Promise<void> {
    return this.loadAnnotationsForPageIndex()
  }

  createFormFieldValue(formFieldValue: FormFieldValue): Promise<void> {
    const content = serializeFormFieldValue(formFieldValue)
    const id = getFormFieldValueID(formFieldValue)

    this._existingFormFieldValuesIds = this._existingFormFieldValuesIds.add(id)

    return this._sync.createRecord(id, content, {})
  }

  async setFormFieldValue(formFieldValue: FormFieldValue): Promise<void> {
    const content = serializeFormFieldValue(formFieldValue)

    try {
      // @ts-expect-error
      return await this._sync.updateRecord(getFormFieldValueID(formFieldValue), content)
    } catch (e) {
      if (!(e instanceof PSPDFKitError)) {
        throw e
      }
    }
  }

  deleteFormFieldValue(id: string): Promise<void> {
    return this._sync.deleteRecord(id).then(() => {
      this._existingFormFieldValuesIds = this._existingFormFieldValuesIds.delete(id)
    })
  }

  loadAnnotationsForPageIndex(): Promise<void> {
    // We only ever start listening to real time changes and emit events when
    // the first page loading was initiated. This will make the behavior of the
    // Instant annotation provider similar to what we do on the others (where we
    // do not pre-download all annotations). This is necessary because we might
    // not handle some callbacks before that.
    //
    // This also has impact on JavaScript support. We now load form fields the
    // same way as annotations when the form designer feature is present which
    // means form fields will not be present until the first pages have been
    // loaded. JavaScript however might interpret document level scripts before
    // that which will cause warnings when form fields are not initialized. See
    // comment in connect.js before calling applyJavaScriptActionChanges.
    //
    // @FIXME(philipp): We need to clean that up and find a way to avoid the
    // push to the next macro queue.
    if (this._loadPromise) {
      return this._loadPromise
    }

    this._loadPromise = new Promise(resolve => setTimeout(resolve, 0)).then(() => {
      if (!this._hasLoadedInitialRecords) {
        // Register callback
        this._sync.onRecordsUpdates(
          // @ts-expect-error

          (c, u, d) => this._onRecordsUpdates(c, u, d, CREATE_REASON_NEW),
          // @ts-expect-error
          (c, u, d) => this._onAcceptedRecords(c, u, d),
        )

        // Handle initial annotations list the same way as an annotations updates callback
        this._onRecordsUpdates(this._sync.getRecords(), List(), List(), CREATE_REASON_LOAD)

        this._hasLoadedInitialRecords = true
      }
    })

    return this._loadPromise
  }

  async loadBookmarks(): Promise<void> {
    return
  }

  syncChanges(): Promise<void> {
    return this._sync.syncChanges()
  }

  /**
   * In a situation where the user has set `disableForms: true` in the configuration,
   * we don't set `this._formFieldCallbacks` and `this._formsEnabledInConfig` is set to `false` but the server doesn't know that and keeps
   * sending the record updates for widgets. In that case we need to filter and remove
   * the record that are widgets if `this.__formsEnabledInConfig` is set to `false`.
   * @param records
   * @private
   */
  _filterRecords(records: List<IRecord>): List<IRecord> {
    // @ts-expect-error
    return records.filter(({ content }) => this._formsEnabledInConfig || !isFormFieldJSON(content))
  }

  /******************************
   * Internal Callback Handlers *
   ******************************/
  _onRecordsUpdates(
    created: List<IRecord>,
    updated: List<IRecord>,
    deleted: List<string>,
    createReason: CreateReason,
  ) {
    let mutableBookmarkAdditions: List<Bookmark> = List()
    const mutableFormFieldAdditions: Array<FormField> = []
    let mutableFormFieldValueAdditions: List<FormFieldValue> = List()
    let mutableCommentAdditions: List<Comment> = List()
    let deletions = Set() // we also remove updated annotations that encountered an error during deserializing

    let bookmarkDeletions = Set() // we also remove updated bookmarks that encountered an error during deserializing

    let formFieldDeletions = Set() // we also remove updated form fields that encountered an error during deserializing

    let formFieldValueDeletions = Set() // we also remove updated form field values that encountered an error during deserializing

    let commentDeletions = Set()
    const state = this._getState ? this._getState() : undefined
    let _created = created
    let _updated = updated
    let _deleted = deleted

    if (state && state.backend && state.backend.isCollaborationPermissionsEnabled()) {
      _created = _created.filter(({ content }) => !!content)

      const updatesIndexToBeRemoved: number[] = []

      updated.forEach((record, index) => {
        if (!record.content) {
          if (isRecordInState(state, record)) {
            // If we receive a record with no content and its present in our store,
            // it means its visibility permission was set to hidden and
            // we delete the record from our store.
            _deleted = _deleted.push(record.id)
            updatesIndexToBeRemoved.push(index)
          } else {
            // If we receive a record with no content and its not present in our store, we do nothing.
            updatesIndexToBeRemoved.push(index)
          }
        } else if (!isRecordInState(state, record)) {
          // If we receive a record with content which isnt present in our store, it means the visibility permission was changed.
          // In that case, we will create a new record.
          _created = _created.push(record)
          updatesIndexToBeRemoved.push(index)
        }
      })
      _updated = _updated.filter((_record, index) => !updatesIndexToBeRemoved.includes(index))
      _deleted = _deleted.filter(
        d =>
          state.annotations.has(d) ||
          this._existingFormFieldValuesIds.has(d) ||
          this._existingFormFieldsIds.has(d) ||
          this._existingCommentIds.has(d) ||
          this._existingBookmarksIds.has(d),
      )
    }

    let additions = List().withMutations(mutableAdditions => {
      this._filterRecords(_created).forEach(({ id, content, permissions, group }) => {
        const options = {
          permissions,
          group,
        }

        try {
          // @ts-expect-error
          if (isFormFieldJSON(content)) {
            // @ts-expect-error
            mutableFormFieldAdditions.push(deserializeFormField(id, content, options))
            this._existingFormFieldsIds = this._existingFormFieldsIds.add(id)
            // @ts-expect-error
          } else if (isFormFieldValueJSON(content)) {
            mutableFormFieldValueAdditions = mutableFormFieldValueAdditions.push(
              // @ts-expect-error
              deserializeFormFieldValue(content),
            )
            this._existingFormFieldValuesIds = this._existingFormFieldValuesIds.add(id)
            // @ts-expect-error
          } else if (isBookmarkJSON(content)) {
            // TODO figure out why when I use mutableBookmarkAdditions asMutable() push produces a list of IDs only 
            mutableBookmarkAdditions = mutableBookmarkAdditions.push(
              // @ts-expect-error
              deserializeBookmark(id, content),
            )
            this._existingBookmarksIds = this._existingBookmarksIds.add(id)
            // @ts-expect-error
          } else if (isCommentJSON(content)) {
            this._existingCommentIds = this._existingCommentIds.add(id)
            mutableCommentAdditions = mutableCommentAdditions.push(
              // @ts-expect-error
              deserializeComment(id, content, options),
            )
            // @ts-expect-error
          } else if (isSignatureInfoJSON(content)) {
            // No-op on digital signatures changes since we won't be syncing signatures
            // on real-time
            // @ts-expect-error
          } else if (isEmbeddedFileJSON(content)) {
            // No-op since web does not support embedded files yet.
          } else {
            // @ts-expect-error
            mutableAdditions.push(deserializeAnnotation(id, content, options))
          }
        } catch (error) {
          info(
            `Skipped creating record #${id} from payload because an error occurred while deserializing.`,
            // @ts-expect-error
            content,
          )
          info(error)
        }
      })
    })
    const showSignatures = state
      ? shouldShowSignatureFields(
          state.features,
          state.signatureFeatureAvailability as ISignatureFeatureAvailability,
        )
      : true

    if (mutableFormFieldAdditions.length > 0) {
      invariant(this._formFieldCallbacks)

      if (state && !showSignatures) {
        // Although Server returns all form fields, if the needed license features
        // combination is not given, we should avoid to add signature form fields.
        // See https://docs.google.com/document/d/1ry0uJIGAgUPa6cmjCWCKRC5zReqdfpROuQyhOt7GXNs/edit#heading=h.dlbwh9b87wv3
        this._formFieldCallbacks.createFormFields(
          List(
            mutableFormFieldAdditions.filter(
              formField => !(formField instanceof SignatureFormField),
            ),
          ),
          createReason,
        )
      } else {
        this._formFieldCallbacks.createFormFields(List(mutableFormFieldAdditions), createReason)
      }
    }

    if (additions.size > 0) {
      invariant(this._annotationCallbacks)

      if (state && !showSignatures) {
        // Don't store widgets associated to ignored form fields on step above
        additions = additions.filter(annotation => {
          if (!(annotation instanceof WidgetAnnotation)) {
            return annotation
          }

          const formField =
            mutableFormFieldAdditions.find(field => field.name === annotation.formFieldName) ||
            state.formFields.get(annotation.formFieldName)

          return !(formField && formField instanceof SignatureFormField)
        })
      }

      this._annotationCallbacks.createAnnotations(additions, Map(), createReason)
    }

    if (mutableFormFieldValueAdditions.size > 0) {
      invariant(this._formFieldValueCallbacks)

      if (state && !showSignatures) {
        // Take into account purposefully ignored form fields from step above
        mutableFormFieldValueAdditions = mutableFormFieldValueAdditions.filter(value => {
          const formField =
            mutableFormFieldAdditions.find(field => field.name === value.name) ||
            // @ts-expect-error
            state.formFields.get(value.formFieldName)

          return !(formField && formField instanceof SignatureFormField)
        })
      }

      this._formFieldValueCallbacks.createFormFieldValues(
        List(mutableFormFieldValueAdditions),
        createReason,
      )
    }

    if (mutableCommentAdditions.size > 0) {
      invariant(this._commentCallbacks)

      this._commentCallbacks.createComments(mutableCommentAdditions, createReason)
    }

    if (mutableBookmarkAdditions.size > 0) {
      invariant(this._bookmarkCallbacks)

      this._bookmarkCallbacks.createBookmarks(mutableBookmarkAdditions, createReason)
    }

    const mutableBookmarkChanges: List<Bookmark> = List().asMutable()
    const mutableFormFieldChanges: Array<FormField> = []
    const mutableFormFieldValueChanges: Array<FormFieldValue> = []
    const mutableCommentChanges: Array<Comment> = []
    const updates = List().withMutations(mutableUpdates => {
      this._filterRecords(_updated).forEach(({ id, content, group, permissions }) => {
        const options = {
          permissions,
          group,
        }

        try {
          // @ts-expect-error
          if (isFormFieldJSON(content)) {
            try {
              // @ts-expect-error
              mutableFormFieldChanges.push(deserializeFormField(id, content, options))
            } catch (error) {
              formFieldDeletions = formFieldDeletions.add(id)
              info(
                `Skipped updating form field #${id} from payload because an error occurred while deserializing. To avoid issues, we have removed the previous version from the application state.`,
                // @ts-expect-error
                content,
              )
              info(error)
            }
            // @ts-expect-error
          } else if (isFormFieldValueJSON(content)) {
            try {
              // @ts-expect-error
              mutableFormFieldValueChanges.push(deserializeFormFieldValue(content))
            } catch (error) {
              formFieldValueDeletions = formFieldValueDeletions.add(id)
              info(
                `Skipped updating form field value #${id} from payload because an error occurred while deserializing. To avoid issues, we have removed the previous version from the application state.`,
                // @ts-expect-error
                content,
              )
              info(error)
            }
            // @ts-expect-error
          } else if (isBookmarkJSON(content)) {
            try {
              // @ts-expect-error
              mutableBookmarkChanges.push(deserializeBookmark(id, content))
            } catch (error) {
              bookmarkDeletions = bookmarkDeletions.add(id)
              info(
                `Skipped updating bookmark #${id} from payload because an error occurred while deserializing. To avoid issues, we have removed the previous version from the application state.`,
                // @ts-expect-error
                content,
              )
              info(error)
            }
            // @ts-expect-error
          } else if (isCommentJSON(content)) {
            try {
              // @ts-expect-error
              mutableCommentChanges.push(deserializeComment(id, content, options))
            } catch (error) {
              commentDeletions = commentDeletions.add(id)
              info(
                `Skipped updating comment #${id} from payload because an error occurred while deserializing. To avoid issues, we have removed the previous version from the application state.`,
                // @ts-expect-error
                content,
                error,
              )
            }
          } else {
            // @ts-expect-error
            mutableUpdates.push(deserializeAnnotation(id, content, options))
          }
        } catch (error) {
          deletions = deletions.add(id)
          info(
            `Skipped updating annotation #${id} from payload because an error occurred while deserializing. To avoid issues, we have removed the previous version from the application state.`,
            // @ts-expect-error
            content,
          )
          info(error)
        }
      })
    })

    if (updates.size > 0) {
      invariant(this._annotationCallbacks)

      this._annotationCallbacks.updateAnnotations(updates)
    }

    if (mutableBookmarkChanges.size > 0) {
      invariant(this._bookmarkCallbacks)

      this._bookmarkCallbacks.updateBookmarks(mutableBookmarkChanges)
    }

    if (mutableFormFieldChanges.length > 0) {
      invariant(this._formFieldCallbacks)

      this._formFieldCallbacks.updateFormFields(List(mutableFormFieldChanges))
    }

    if (mutableFormFieldValueChanges.length > 0) {
      invariant(this._formFieldValueCallbacks)

      this._formFieldValueCallbacks.setFormFieldValues(List(mutableFormFieldValueChanges))
    }

    if (mutableCommentChanges.length > 0) {
      invariant(this._commentCallbacks)

      this._commentCallbacks.updateComments(List(mutableCommentChanges))
    }

    deletions = deletions.concat(
      _deleted
        .filter(
          id =>
            !this._existingBookmarksIds.has(id) &&
            !this._existingFormFieldsIds.has(id) &&
            !this._existingFormFieldValuesIds.has(id) &&
            !this._existingCommentIds.has(id),
        )
        .toSet(),
    )

    if (deletions.size > 0) {
      invariant(this._annotationCallbacks)

      this._annotationCallbacks.deleteAnnotations(deletions)
    }

    bookmarkDeletions = bookmarkDeletions.concat(
      _deleted
        .filter(id => {
          const isBookmarkDeletion = this._existingBookmarksIds.has(id)

          if (isBookmarkDeletion) {
            this._existingBookmarksIds = this._existingBookmarksIds.delete(id)
          }

          return isBookmarkDeletion
        })
        .toSet(),
    )

    if (bookmarkDeletions.size > 0) {
      invariant(this._bookmarkCallbacks)

      this._bookmarkCallbacks.deleteBookmarks(bookmarkDeletions)
    }

    formFieldDeletions = formFieldDeletions.concat(
      _deleted
        .filter(id => {
          const isFormFieldDeletion = this._existingFormFieldsIds.has(id)

          if (isFormFieldDeletion) {
            this._existingFormFieldsIds = this._existingFormFieldsIds.delete(id)
          }

          return isFormFieldDeletion
        })
        .toSet(),
    )

    if (formFieldDeletions.size > 0) {
      invariant(this._formFieldCallbacks)

      this._formFieldCallbacks.deleteFormFields(formFieldDeletions)
    }

    formFieldValueDeletions = formFieldValueDeletions.concat(
      _deleted
        .filter(id => {
          const isFormFieldValueDeletion = this._existingFormFieldValuesIds.has(id)

          if (isFormFieldValueDeletion) {
            this._existingFormFieldValuesIds = this._existingFormFieldValuesIds.delete(id)
          }

          return isFormFieldValueDeletion
        })
        .toSet(),
    )

    if (formFieldValueDeletions.size > 0) {
      invariant(this._formFieldValueCallbacks)

      this._formFieldValueCallbacks.deleteFormFieldValues(formFieldValueDeletions)
    }

    commentDeletions = commentDeletions.concat(
      _deleted
        .filter(id => {
          const commentDeletion = this._existingCommentIds.has(id)

          if (commentDeletion) {
            this._existingCommentIds = this._existingCommentIds.delete(id)
          }

          return commentDeletion
        })
        .toSet(),
    )

    if (commentDeletions.size > 0) {
      invariant(this._commentCallbacks)

      this._commentCallbacks.deleteComments(commentDeletions)
    }
  }

  _onAcceptedRecords(created: List<IRecord>, updated: List<IRecord>) {
    const state = this._getState ? this._getState() : undefined

    if (!state || !state.backend || !state.backend.isCollaborationPermissionsEnabled()) {
      return
    }

    // When Collaboration Permissions are enabled we need to update the locally created or updated
    // records so that they include the proper permissions enforced by the backend.
    const annotationsUpdates: Array<Annotation> = []
    const commentsUpdates: Array<Comment> = []
    const formFieldsUpdates: Array<FormField> = []
    // We also need to remove records from the Redux store if Server doesn't assign view
    // permissions
    const deletableAnnotations: Array<AnnotationID> = []
    const deletableComments: Array<BookmarkID> = []
    const deletableFormFields: Array<CommentID> = []

    if (!created.isEmpty()) {
      created.forEach(mergePermissionsOnRecords)
    }

    if (!updated.isEmpty()) {
      updated.forEach(mergePermissionsOnRecords)
    }

    if (annotationsUpdates.length > 0) {
      invariant(this._annotationCallbacks)

      // @ts-expect-error
      this._annotationCallbacks.updateAnnotations(List(annotationsUpdates), true)
    }

    if (commentsUpdates.length > 0) {
      invariant(this._commentCallbacks)

      this._commentCallbacks.updateComments(List(commentsUpdates))
    }

    if (formFieldsUpdates.length > 0) {
      invariant(this._formFieldCallbacks)

      this._formFieldCallbacks.updateFormFields(List(formFieldsUpdates))
    }

    if (deletableAnnotations.length > 0) {
      invariant(this._annotationCallbacks)

      // @ts-expect-error
      this._annotationCallbacks.deleteAnnotations(Set(deletableAnnotations), true)
    }

    if (deletableComments.length > 0) {
      invariant(this._commentCallbacks)

      this._commentCallbacks.deleteComments(Set(deletableComments))
    }

    if (deletableFormFields.length > 0) {
      invariant(this._formFieldCallbacks)

      this._formFieldCallbacks.deleteFormFields(Set(deletableFormFields))
    }

    function mergePermissionsOnRecords(record: IRecord) {
      const options = {
        permissions: record.permissions,
        group: record.group,
      }
      let entity = getRecordInState(state as State, record)

      // We can ignore form field values responses (string type)
      if (entity && typeof entity !== 'string') {
        validateCollaborationPermissionJSON(options)

        const collaborationPermissions = getCollaborationPermissionsFromJSON(options)

        entity = entity.merge(collaborationPermissions)

        if (entity instanceof Annotation) {
          if (options.permissions && options.permissions.view) {
            annotationsUpdates.push(entity)
          } else {
            deletableAnnotations.push(entity.id)
          }
        } else if (entity instanceof Comment) {
          if (options.permissions && options.permissions.view) {
            commentsUpdates.push(entity)
          } else {
            invariant(entity.id)
            deletableComments.push(entity.id)
          }
        } else if (entity instanceof FormField) {
          if (options.permissions && options.permissions.view) {
            formFieldsUpdates.push(entity)
          } else {
            deletableFormFields.push(entity.id)
          }
        }
      }
    }
  }

  /**
   * Sets the on clients change callback. Will be overwritten when another callback is set.
   */
  onClientsChange(onClientsChangeCallback: (clients: Map<string, Client>) => void): void {
    if (typeof onClientsChangeCallback !== 'function') {
      throw new TypeError('Callback must be a function')
    }

    this.onClientsChangeCallback = onClientsChangeCallback
  }

  _onClientsChange(clients: Map<string, Client>): void {
    this.onClientsChangeCallback.call(null, clients)
  }

  /**
   * Document handle conflict callback handlers.
   */

  /**
   * Set by ServerBackend after instantiating the provider
   */
  setDocumentHandleConflictCallback = (callback: () => void) => {
    this._documentHandleConflictCallback = callback
  }

  /**
   * Set in utils/document by setupManagers() on connect and reconnect
   */
  setDocumentHandleOutdated = (callback: (arg0: boolean) => void) => {
    this._setDocumentHandleOutdatedCallback = callback
  }

  /**
   * Callback passed to InstantSync when instantiating it
   */
  onDocumentHandleConflict = () => {
    this._documentHandleConflictCallback && this._documentHandleConflictCallback()
    this._setDocumentHandleOutdatedCallback && this._setDocumentHandleOutdatedCallback(true)
  }
}
