import { List, Map, OrderedMap, Set } from 'immutable'
import type RecordChangeRequest from './RecordChangeRequest'
import type Client from '../ClientsPresence/models/Client'
import * as Immutable from 'immutable'

export type InstantSyncAuthPayload = {
  auth_token: string
}

interface IInstantSyncState {
  requestInfo: {
    serverURL: string
    authPayload: InstantSyncAuthPayload
  } | null
  status: 'offline' | 'connecting' | 'online'
  currentClient: Client | null
  localRecordsContents: OrderedMap<string, Record<string, any>>
  localRecordsChanges: List<RecordChangeRequest>
  stagedRecordsChanges: List<RecordChangeRequest>
  localRecordsRev: number
  requiredAttachmentIds: Set<string>
  clients: Map<string, Client>
}

/**
 * InstantSyncState
 *
 * Describes current state of InstantSync
 */

export default class InstantSyncState extends Immutable.Record<IInstantSyncState>({
  requestInfo: null,
  status: 'offline',
  currentClient: null,
  localRecordsContents: OrderedMap(),
  localRecordsChanges: List(),
  stagedRecordsChanges: List(),
  localRecordsRev: 0,
  requiredAttachmentIds: Set(),
  clients: Map(),
}) {
  /**
   * Holds the endpoint URL (serverURL) and authentication token to make requests.
   */
  override requestInfo: {
    serverURL: string
    authPayload: InstantSyncAuthPayload
  }

  /**
   * Indication of whether the client is connected to the server or not. Can either be 'offline',
   * 'connecting' or 'online'.
   */
  override status: 'offline' | 'connecting' | 'online'
  override currentClient: Client

  /**
   * Holds a map of serialized object contents, which are in sync with the server and have no conflicts
   */
  override localRecordsContents: OrderedMap<string, Record<string, any>>

  /**
   * Holds a list for changes that have been made locally and have not yet been saved on the server.
   * For every change request (create, update, delete), we will create a new `RecordChangeRequest`
   * object. This requires us to map every change request to the individual change set and makes
   * it possible to support changing multiple records at once.
   */
  override localRecordsChanges: List<RecordChangeRequest>

  /**
   * Holds a list for changes that have been made locally and are currently being saved on the server.
   * For every change request (create, update, delete), we will create a new `RecordChangeRequest`
   * object. This requires us to map every change request to the individual change set and makes
   * it possible to support changing multiple records at once.
   */
  override stagedRecordsChanges: List<RecordChangeRequest>

  /**
   * Holds the latest revision of the `localRecordsContents`.
   */
  override localRecordsRev: number

  /**
   * All attachments ids, that need to be attached to the next request.
   */
  override requiredAttachmentIds: Set<string>

  /**
   * A list of all connect clients of every user that is currently present in the record.
   */
  override clients: Map<string, Client>
}
