import * as Immutable from 'immutable'

interface IClient {
  clientId: string
  userId: string | null
  presenceContent: Record<string, unknown>
}

export default class Client extends Immutable.Record<IClient>({
  clientId: '',
  userId: null,
  presenceContent: {},
}) {
  /**
   * Holds the client ID as a string. A unique ID is generated for every client.
   *
   * @member {string} clientId
   */
  override clientId: string

  /**
   * Holds the user ID of the client. This ID is supplied by your backend. If the value is `null`,
   * the client does not belong to a user and thus is considered anonymous.
   *
   * @member {?string} userId
   */
  override userId: string | null

  /**
   * Holds the latest presence content a client has announced. This can be an arbitrary object.
   *
   * @member {Object} presenceContent
   */
  override presenceContent: Record<string, any>
}
