import { Class } from 'utility-types'
import { List, Map } from 'immutable'
import { PSPDFKitError } from '@pspdfkit/shared'
import Client from './models/Client'
import ClientsPresenceState from './models/ClientsPresenceState'
import WebsocketConnection from '../WebsocketConnection/WebsocketConnection'
import setClientsAction from './actions/setClientsAction'
import applyClientChangesAction from './actions/applyClientChangesAction'
import updateCurrentClientPresenceAction from './actions/updateCurrentClientPresenceAction'

export default class ClientsPresence {
  connection: WebsocketConnection
  state: ClientsPresenceState
  setState: (arg0: ClientsPresenceState) => void
  connectionClass: Class<any>
  clientUpdatesCallback: (
    arg0: Map<string, Client>,
    arg1: Map<string, Client>,
    arg2: List<string>,
  ) => void
  shouldFireClientUpdatesCallback: boolean

  /**
   * For easier testing, we allow a custom state in the constructor.
   */
  constructor(
    state: ClientsPresenceState = new ClientsPresenceState(),
    connectionClass: Class<any> = WebsocketConnection,
  ) {
    this.state = state
    this.connectionClass = connectionClass
  }

  /**
   * This method should be called right after the constructor.
   */
  load(
    serverURL: string,
    authPayload: Record<string, any>,
    initialPresenceContent: Record<string, any>,
  ): Promise<ClientsPresence> {
    return new Promise((resolve, reject) => {
      this.setState = (newState: ClientsPresenceState): void => {
        this.state = newState
      }

      this.connection = new this.connectionClass(serverURL, authPayload, {
        events: ['client_presence'],
      })
      this.connection.on(
        'connect',
        // @ts-expect-error
        (session: { clientId: string; userId?: string | null }) => {
          const currentClient = new Client({
            clientId: session.clientId,
            userId: session.userId,
            presenceContent: initialPresenceContent,
          })

          this.setState(this.state.set('status', 'online').set('currentClient', currentClient))
          this.populateClients(initialPresenceContent)
            .then(() => {
              resolve(this)
            })
            .catch(reject)
        },
      )
      this.connection.on('error', message => {
        reject(new PSPDFKitError(message.toString()))
      })
      this.connection.on('client_presence', payload => this.onInfoClientPresence(payload))
      this.connection.connect()
    })
  }

  populateClients(presenceContent: Record<string, any>): Promise<any> {
    return new Promise<void>((resolve, reject) => {
      const enterZonePayload = {
        presence: presenceContent,
      }

      this.connection
        .sendRequest('enter_layer', enterZonePayload)
        .then(payload => {
          this.setState(setClientsAction(this.state, payload))
          resolve()
        })
        .catch(reject)
    })
  }

  onInfoClientPresence(payload: Record<string, any>): void {
    this.setState(applyClientChangesAction(this.state, payload))

    if (this.shouldFireClientUpdatesCallback) {
      let entered: Map<string, Client> = Map()

      if (payload.clients.entered) {
        const enteredClientIds = payload.clients.entered.map(c => c.client_id)

        entered = this.state.clients
          .filter(client => {
            return enteredClientIds.indexOf(client.clientId) !== -1
          })
          .toMap()
      }

      let updated: Map<string, Client> = Map()

      if (payload.clients.updated) {
        const updatedClientIds = payload.clients.updated.map(c => c.client_id)

        updated = this.state.clients
          .filter(client => {
            return updatedClientIds.indexOf(client.clientId) !== -1
          })
          .toMap()
      }

      let left = List()

      if (payload.clients.updated) {
        left = List(payload.clients.left)
      }

      this.clientUpdatesCallback(entered, updated, left)
    }
  }

  /**
   * Close the current connection and stop reconnecting attempts.
   *
   * @returns {void}
   */
  disconnect(): void {
    if (this.getStatus() === 'offline') {
      return
    }

    this.setState(this.state.set('status', 'offline'))
    this.connection.disconnect()
  }

  /**
   * Indication of whether the client is connected to the server or not.
   *
   * @returns {string} A status indicating the connection status, either `offline`, `connecting`, or
   *         `online`.
   */
  getStatus(): 'offline' | 'connecting' | 'online' {
    return this.state.status
  }

  /**
   * Information about the currently connected client.
   *
   * @returns {Client} An object that holds information about the currently connected client.
   */
  getCurrentClient(): Client {
    return this.state.currentClient as Client
  }

  /*********************
   * Managing Presence *
   *********************/

  /**
   * Returns a map of all connected clients of every user that is currently present in the zone.
   *
   * Before this method is called the first time, we will never fire a
   * {@link ClientsPresence#onClientUpdates} callback. The reason for this is to give you time to set
   * your callbacks, without missing out on information.
   *
   * @returns {Immutable.Map.<string, Client>} List of all connected clients form all users
   */
  getClients(): Map<string, Client> {
    this.shouldFireClientUpdatesCallback = true

    return this.state.clients
  }

  /**
   * Updates the presence content for the zone of the current connection. The presence content is
   * being broadcast to all other connected clients.
   *
   * When the server accepts our changes, we will resolve the `Promise` with `true`. In case of any
   * error, we will reject with `PSPDFKitError`.
   *
   * @param {Object} presenceContent - An arbitrary object that describes the presence content.
   * @returns {Promise.<boolean, PSPDFKitError>} A Promise that that will resolve with `true` or reject
   *         with a `PSPDFKitError`.
   */
  updatePresence(presenceContent: Record<string, any>): Promise<boolean> {
    return new Promise((resolve, reject) => {
      if (this.getStatus() !== 'online') {
        return reject(new PSPDFKitError('ClientsPresence is not connected'))
      }

      const payload = {
        presence: presenceContent,
      }

      this.connection.sendRequest('update_client_presence', payload).then(
        () => {
          this.setState(updateCurrentClientPresenceAction(this.state, presenceContent))
          resolve(true)
        },
        () => {
          reject(new PSPDFKitError('Unable to update presence'))
        },
      )
    })
  }

  /**********************
   * Presence Callbacks *
   **********************/

  /**
   * This callback will be triggered whenever the client map will be updated. It will contain
   * the change set of entered, updated and left clients.
   *
   * It will not fire without you calling {@link ClientsPresence#getClients} before.
   *
   * @callback clientUpdatesCallback
   * @param {Immutable.Map.<string, Client>} entered - A map of all clients that have entered
   *        the zone
   * @param {Immutable.Map.<string, Client>} updated - A map of all clients that published
   *        presence updates.
   * @param {Immutable.List.<string>} left - A list of all clients IDs that have disconnected from
   *        this zone.
   */

  /**
   * Sets a new clients update callback. When a callback is already set, it will overwrite the
   * existing one (only one callback is registered to a ClientsPresence instance at any time).
   *
   * This callback should be registered before you call {@link ClientsPresence#getClients}. It will only
   * fire after {@link ClientsPresence#getClients} is called the first times.
   *
   * The callback can never be set to `null` because otherwise your application could miss out on
   * updates.
   *
   * @throws {TypeError} when the supplied `callback` is not a function
   * @param {clientUpdatesCallback} callback - The callback that handles the clients map updates
   */
  onClientUpdates(
    callback: (
      entered: Map<string, Client>,
      updated: Map<string, Client>,
      left: List<string>,
    ) => void,
  ) {
    if (typeof callback !== 'function') {
      throw new TypeError('callback must be a function')
    }

    this.clientUpdatesCallback = callback
  }
}
