import { Map } from 'immutable'
import type Client from './Client'
import * as Immutable from 'immutable'

interface IClientsPresenceState {
  /**
   * Indication of whether the client is connected to the server or not. Can either be 'offline',
   * 'connecting' or 'online'.
   *
   * @member {string} status
   */
  status: 'offline' | 'connecting' | 'online'
  currentClient: null | Client

  /**
   * A list of all connect clients of every user that is currently present in the record.
   *
   * @member {Immutable.Map.<string, Client>} presence
   */
  clients: Map<string, Client>
}

/**
 * ClientsPresenceState
 *
 * Describes current state of ClientsPresence
 */

export default class ClientsPresenceState extends Immutable.Record<IClientsPresenceState>({
  status: 'offline',
  currentClient: null,
  clients: Map(),
}) {}
