import { clientFromPayload } from '../utils/clients'
import { Map } from 'immutable'
import type ClientsPresenceState from '../models/ClientsPresenceState'
import { invariant } from '../../../utils/invariant'
import Client from '../models/Client'

/**
 * This action overwrites the current clients map with the `payload`. This is used, whenever we
 * `enter_layer`
 */

export default function setClientsAction(
  state: ClientsPresenceState,
  payload: Record<string, any>,
): ClientsPresenceState {
  invariant(payload.clients, 'The payload must have a `clients` list')

  return state.withMutations(mutableState => {
    const clients = Map(
      payload.clients
        .map(clientPayload => clientFromPayload(clientPayload))
        .map(c => [c.clientId, c]),
    ).set(state.currentClient?.clientId, state.currentClient) as Map<string, Client>

    mutableState.set('clients', clients)
  })
}
