import { clientFromPayload } from '../utils/clients'
import { PSPDFKitError } from '@pspdfkit/shared'
import type ClientsPresenceState from '../models/ClientsPresenceState'
import { invariant } from '../../../utils/invariant'

/**
 * This action applies the change set from the `info:client_presence` message.
 *
 * @throws {PSPDFKitError} when we detect a failed state
 */

export default function applyClientChangesAction(
  state: ClientsPresenceState,
  payload: Record<string, any>,
): ClientsPresenceState {
  invariant(typeof payload.clients == 'object', 'The payload must have `clients`')

  const newClients = state.clients.withMutations(mutableClients => {
    if (payload.clients.entered) {
      for (const clientPayload of payload.clients.entered) {
        if (state.clients.has(clientPayload.client_id)) {
          throw new PSPDFKitError('The client marked as entered is already known')
        }

        const client = clientFromPayload(clientPayload)

        mutableClients.set(client.clientId, client)
      }
    }

    // On updated clients, only the `presenceContent` can change
    if (payload.clients.updated) {
      for (const clientPayload of payload.clients.updated) {
        invariant(
          typeof clientPayload.client_id === 'string',
          'The client payload must have a `client_id`',
        )
        invariant(
          typeof clientPayload.presence === 'object',
          'The client payload must have a `presence`',
        )

        const client = state.clients.get(clientPayload.client_id)

        if (!client) {
          throw new PSPDFKitError('The client marked as updated is not known')
        }

        mutableClients.set(client.clientId, client.set('presenceContent', clientPayload.presence))
      }
    }

    if (payload.clients.left) {
      for (const clientId of payload.clients.left) {
        if (!state.clients.has(clientId)) {
          throw new PSPDFKitError('The client marked as left is not known')
        }

        mutableClients.delete(clientId)
      }
    }
  })

  return state.set('clients', newClients)
}
