import { Class } from 'utility-types'
import { List } from 'immutable'
import { mergeChanges } from './utils'
import { PSPDFKitError } from '@pspdfkit/shared'
import RecordChangeRequest from './RecordChangeRequest'
import InstantSyncCycle from './InstantSyncCycle'
import InstantSyncState from './InstantSyncState'
import type { InstantSyncAuthPayload } from './InstantSyncState'
import type { InstantSyncResponseChanges } from './InstantSyncCycle'
import type { IRecord } from './InstantProvider'
import { DEFAULT_LONG_POLLING_TIMEOUT } from './InstantSyncCycle'
import { debounceAsync } from '@/utils/debounce'

export default class InstantSync {
  /************************
   * Record operations
   ************************/
  getRecords(): List<IRecord> {
    this._shouldFireRecordsUpdateCallback = true

    return this._state.localRecordsContents
      .map((record, id) => {
        const { content, permissions, group } = record

        return {
          content,
          permissions,
          group,
          id,
        }
      })
      .toList()
  }

  createRecord(
    id: string,
    content: Record<string, any>,
    attachments: Record<string, Blob>,
    group?: string,
  ): Promise<void> {
    return new Promise((resolve, reject) => {
      const change = new RecordChangeRequest({
        id,
        content,
        attachments,
        group,
        type: 'created',
        // @ts-expect-error
        resolve,
        reject,
      })

      this.enqueueChangeRequest(change)
    })
  }

  updateRecord(
    id: string,
    content?: Record<string, any>,
    group?: string,
  ): Promise<Record<string, any>> {
    return new Promise((resolve, reject) => {
      if (!this.isKnownRecordId(id)) {
        return reject(new PSPDFKitError(`Record with ID: ${id} not found.`))
      }

      const change = new RecordChangeRequest({
        id,
        content,
        group,
        type: 'updated',
        // @ts-expect-error
        resolve,
        reject,
      })

      this.enqueueChangeRequest(change)
    })
  }

  deleteRecord(id: string): Promise<boolean> {
    return new Promise((resolve, reject) => {
      if (!this.isKnownRecordId(id)) {
        return reject(new PSPDFKitError(`Record with ID: ${id} not found.`))
      }

      const change = new RecordChangeRequest({
        id,
        type: 'deleted',
        // @ts-expect-error
        resolve,
        reject,
      })

      this.enqueueChangeRequest(change)
    })
  }

  onRecordsUpdates(
    recordsUpdateCallback: (
      created: List<Record<string, any>>,
      updated: List<Record<string, any>>,
      deleted: List<string>,
    ) => void,
    acceptedRecordsCallback: (
      created: List<Record<string, any>>,
      updated: List<Record<string, any>>,
      deleted: List<string>,
    ) => void,
  ) {
    if (typeof recordsUpdateCallback !== 'function') {
      throw new TypeError('recordsUpdateCallback must be a function')
    }

    if (typeof acceptedRecordsCallback !== 'function') {
      throw new TypeError('acceptedRecordsCallback must be a function')
    }

    this._recordsUpdatesCallback = recordsUpdateCallback
    this._acceptedRecordsResponseCallback = acceptedRecordsCallback
  }

  destroy() {
    if (this._cycle) {
      this._cycle.destroy()
    }
  }

  /**************************
   * Private Implementation *
   **************************/
  _state: InstantSyncState
  _cycle: InstantSyncCycle
  _CycleClass: Class<InstantSyncCycle>
  _recordsUpdatesCallback: (
    created: List<Record<string, any>>,
    updated: List<Record<string, any>>,
    deleted: List<string>,
  ) => void = () => {}
  _acceptedRecordsResponseCallback: (
    created: List<Record<string, any>>,
    updated: List<Record<string, any>>,
    deleted: List<string>,
  ) => void = () => {}
  _shouldFireRecordsUpdateCallback = false

  /**
   * For easier testing, we allow a custom state and Cycle class in the constructor.
   */
  constructor(
    state: InstantSyncState = new InstantSyncState(),
    CycleClass: Class<InstantSyncCycle> = InstantSyncCycle,
  ) {
    this._state = state
    this._CycleClass = CycleClass
  }

  /**
   * This method should be called right after the constructor. See {@link InstantSync.load} for the
   * public abstraction.
   */
  load(
    serverURL: string,
    authPayload: InstantSyncAuthPayload,
    listenToServerChangesEnabled = true,
  ): Promise<InstantSync> {
    return new Promise((resolve, reject) => {
      const getState = () => this._state

      const setState = this.setState.bind(this)

      this.setState(
        this._state.set('requestInfo', {
          serverURL,
          authPayload,
        }),
      )
      this._cycle = new this._CycleClass({
        getState,
        setState,
        onChanges: this.onChanges,
        onAcceptedRecords: this.onAcceptedRecords,
        longPollingTimeout: listenToServerChangesEnabled ? DEFAULT_LONG_POLLING_TIMEOUT : 0,
      })

      // Executing the runCycle here also means that we will do the initial synchronization
      // with the server and fetch the records.
      this._cycle // We force a timeout of `0` here since we don't want the initial loading to have a delay
        .nextCycle(0)
        .then(() => {
          resolve(this)
        })
        .catch(reject)
    })
  }

  setState(newState: InstantSyncState): void {
    this._state = newState
  }

  onChanges = (changes: InstantSyncResponseChanges): void => {
    if (this._shouldFireRecordsUpdateCallback) {
      const { created, updated, deleted } = changes

      this._recordsUpdatesCallback(List(created), List(updated), List(deleted))
    }
  }
  onAcceptedRecords = (accepted: InstantSyncResponseChanges): void => {
    if (this._shouldFireRecordsUpdateCallback) {
      const { created, updated, deleted } = accepted

      this._acceptedRecordsResponseCallback(List(created), List(updated), List(deleted))
    }
  }
  setOnDocumentHandleConflictCallback = (callback: () => void): void => {
    if (typeof callback !== 'function') {
      throw new TypeError('callback must be a function')
    }

    this._cycle.setOnDocumentHandleConflictCallback(callback)
  }

  enqueueChangeRequest(changeRequest: RecordChangeRequest): void {
    const mergedRecordsChanges = mergeChanges({
      oldChanges: this._state.localRecordsChanges,
      newChanges: List([changeRequest]),
    })

    this.setState(this._state.set('localRecordsChanges', mergedRecordsChanges))
  }

  /**
   * Performs next sync cycle, pushing `localRecordsChanges` and pulling remote changes.
   */
  syncChanges: () => Promise<void> = debounceAsync(() => this._cycle.nextCycle())

  isKnownRecordId(id: string): boolean {
    function createdChangeWithSameId(change: RecordChangeRequest): boolean {
      return change.type === 'created' && change.id === id
    }

    const isSyncedRecord = this._state.localRecordsContents.has(id)

    const isLocalRecord = !!this._state.localRecordsChanges.find(createdChangeWithSameId)
    const isStagedRecord = !!this._state.stagedRecordsChanges.find(createdChangeWithSameId)

    return isSyncedRecord || isLocalRecord || isStagedRecord
  }
}
